<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 设备类型名称 -->
            <a-form-model-item
              label="设备类型名称"
              prop="resourceTypeName"
              ref="resourceTypeName"
            >
              <a-input
                v-model="entity.resourceTypeName"
                placeholder="请输入设备类型名称"
                name="resourceTypeName"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 所属设备家族 -->
            <a-form-model-item
              label="所属设备家族"
              prop="resourceFamilyId"
              ref="resourceFamilyId"
            >
              <a-select
                v-model="entity.resourceFamilyId"
                placeholder="请选择所属设备家族"
                name="resourceFamilyId"
              >
                <a-select-option v-for="item in familyList" :key="item.id" :value="item.id">
                  {{ item.name }}
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 制造商 -->
            <a-form-model-item
              label="制造商"
              prop="manufacturer"
              ref="manufacturer"
            >
              <a-select
                v-model="entity.manufacturer"
                placeholder="请选择制造商"
                name="manufacturer"
                allowClear
                :options="manufacturerOptions"
              >
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 型号 -->
            <a-form-model-item
              label="型号"
              prop="model"
              ref="model"
            >
              <a-input
                v-model="entity.model"
                placeholder="请输入型号"
                name="model"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-select
                v-model="entity.status"
                placeholder="请选择状态"
                name="status"
              >
                <a-select-option value="启用">启用</a-select-option>
                <a-select-option value="禁用">禁用</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="18">
            <!-- 规格参数 -->
            <a-form-model-item
              label="规格参数"
              prop="specifications"
              ref="specifications"
            >
              <a-input
                v-model="entity.specifications"
                placeholder="请输入规格参数"
                name="specifications"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <!-- 描述信息 -->
            <a-form-model-item label="描述" prop="description">
              <a-textarea
                v-model:value="entity.description"
                placeholder="请输入描述信息"
                :rows="4"
                :max-length="1000"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>
      <g-collapse headerName="技术参数">
        <template #content>
        <a-form-model-item label="">
          <div class="tech-params-tools">
            
            <a-button type="default" size="small" @click="addTechParam" style="margin-left: 8px;">
              <a-icon type="plus" /> 添加参数
            </a-button>
          </div>
        </a-form-model-item>
        <a-table
          :columns="techParamColumns"
          :data-source="entity.techParams"
          :pagination="false"
          size="small"
          bordered
        >
            <template slot="paramName" slot-scope="text, record, index">
              <a-input v-model="record.paramName" placeholder="参数名称" />
            </template>
            <template slot="paramValue" slot-scope="text, record, index">
              <a-input v-model="record.paramValue" placeholder="参数值" />
            </template>
            <template slot="action" slot-scope="text, record, index">
              <a-button type="link" @click="removeTechParam(index)" danger>
                <a-icon type="delete" /> 删除
              </a-button>
            </template>
        </a-table>
      </template>
      </g-collapse>
      <g-collapse headerName="详细信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
    
    
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditResourceType",
  mixins: [GNdoPageEditBase],
  data() {
    return {
      entity: {
        resourceTypeName: "",
        resourceFamilyId: "",
        manufacturer: "",
        model: "",
        specifications: "",
        status: "启用",
        description: "",
        techParams: [], // 技术参数列表
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      },
      familyList: [], // 设备家族列表
      manufacturerOptions: [
        { value: "华为", label: "华为" },
        { value: "思科", label: "思科" },
        { value: "惠普", label: "惠普" },
        { value: "戴尔", label: "戴尔" },
        { value: "IBM", label: "IBM" },
        { value: "联想", label: "联想" },
        { value: "浪潮", label: "浪潮" },
        { value: "新华三", label: "新华三" },
        { value: "锐捷", label: "锐捷" },
      ],
      techParamColumns: [
        {
          title: "参数名称",
          dataIndex: "paramName",
          key: "paramName",
          width: "30%",
          scopedSlots: { customRender: "paramName" },
        },
        {
          title: "参数值",
          dataIndex: "paramValue",
          key: "paramValue",
          width: "50%",
          scopedSlots: { customRender: "paramValue" },
        },
        {
          title: "操作",
          dataIndex: "action",
          key: "action",
          width: "20%",
          scopedSlots: { customRender: "action" },
        },
      ],
      rules: {
        resourceTypeName: [
          { required: true, message: "设备类型名称不能为空", trigger: "change" },
          {
            pattern: /^[^\s]+(\s+[^\s]+)*$/,
            trigger: "change",
            message: "名称格式不正确，不能包含前后空格",
          },
        ],
        resourceFamilyId: [
          { required: true, message: "所属设备家族不能为空", trigger: "change" }
        ],
        manufacturer: [
          { required: true, message: "制造商不能为空", trigger: "change" }
        ],
        model: [
          { required: true, message: "型号不能为空", trigger: "change" }
        ],
        status: [
          { required: true, message: "状态不能为空", trigger: "change" }
        ]
      },
    };
  },

  mounted() {
    this.getFamilyList();
  },

  methods: {
    // 获取设备家族列表
    getFamilyList() {
      // 模拟获取设备家族列表数据
      setTimeout(() => {
        this.familyList = [
          { id: 1, name: "交换机" },
          { id: 2, name: "路由器" },
          { id: 3, name: "服务器" },
          { id: 4, name: "存储设备" },
          { id: 5, name: "防火墙" }
        ];
      }, 300);
    },
    // 添加技术参数
    addTechParam() {
      if (!this.entity.techParams) {
        this.entity.techParams = [];
      }
      this.entity.techParams.push({
        key: Date.now(),
        paramName: "",
        paramValue: ""
      });
    },
    
    // 删除技术参数
    removeTechParam(index) {
      this.entity.techParams.splice(index, 1);
    },
    
    // 初始化
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取设备类型数据
        setTimeout(() => {
          this.loading = false;

          this.entity = {
            resourceTypeName: "华为 S5820-32C-EI 交换机",
            resourceFamilyId: 1,
            manufacturer: "华为",
            model: "S5820-32C-EI",
            specifications: "1U机架式，冗余电源，32个千兆电口，4个万兆光口",
            status: "启用",
            description: "华为S5820系列千兆企业交换机，适用于中大型企业网络核心层",
            techParams: [
              { key: 1, paramName: "端口数量", paramValue: "32个GE电口 + 4个10GE SFP+光口" },
              { key: 2, paramName: "交换容量", paramValue: "598Gbps" },
              { key: 3, paramName: "包转发率", paramValue: "222Mpps" },
              { key: 4, paramName: "MAC地址表", paramValue: "32K" },
              { key: 5, paramName: "电源", paramValue: "交流100-240V 50/60Hz，冗余电源" },
            ],
            createdBy: "admin",
            createdTime: "2025-03-18 10:30",
            modifiedBy: "admin",
            modifiedTime: "2025-03-18 11:55"
          };
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    
    resetFormData() {
      // 重置表单绑定的数据源
      this.entity = {
        resourceTypeName: "",
        resourceFamilyId: "",
        manufacturer: "",
        model: "",
        specifications: "",
        status: "启用",
        description: "",
        techParams: [],
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      };
    },
    
    async save() {
      // 保存 校验
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.resourceTypeName);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e){
      console.log(e.target.value);
    }
  },
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.tech-params-tools {
  display: flex;
}
</style> 